<template>
  <div class="wheelWrapper">
    <canvas :width="canvasWidth" :height="canvasHeight" id="wheel"></canvas>
    <div class="btn" @click="doRotate">
      Go!
    </div>
  </div>

</template>

<script>
const angel = Math.PI / 180;

export default {
  props: ["data"],
  data() {
    return {
      canvasHeight: 800,
      canvasWidth: 800
    };
  },
  watch: {
    data() {
      this.doDraw();
    }
  },
  mounted() {
    this.doDraw();
    // ctx.lineTo(100, 20);
  },
  methods: {
    doRotate() {
      const res = Math.round(Math.random() * this.data.length);
      let step = 0;
      console.log(res);
      const time = setInterval(() => {
        this.doDraw(step % 360);
        step++;
      }, 30);
      setTimeout(() => {
        clearInterval(time);
        step = 0;
        this.doDraw(this.data.length - res);
        // alert(this.data[this.data.length - res]);
      }, 5000);
    },
    doDraw(step) {
      console.log(step);
      const colorList = ["#0092C7", "#9FE0F6", "#f3e59a", "#f3b59b", "#f29c9c"];
      const canvas = document.getElementById("wheel");
      const ctx = canvas.getContext("2d");
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      // ctx.fillText("123567723424312", 100, 100);

      let endAngel = "";
      const r = 300;
      const arc = 360 / this.data.length * angel;

      let stepArc = 0;
      if (step !== undefined) {
        stepArc = step * arc;
      }

      let startAngel = 270 * angel - arc / 2 + stepArc;
      const ZeroX = canvas.width / 2;
      const ZeroY = canvas.height / 2;

      this.data.forEach((key, index) => {
        ctx.beginPath();
        ctx.fillStyle = colorList[index % colorList.length];
        ctx.moveTo(ZeroX, ZeroY);
        endAngel = startAngel + arc;
        ctx.arc(ZeroX, ZeroY, r, startAngel, endAngel, false); // 创建弧
        startAngel = endAngel;
        ctx.lineTo(ZeroX, ZeroY);
        ctx.closePath();
        ctx.fill();
        ctx.save();
        ctx.translate(ZeroX, ZeroY);
        ctx.fillStyle = "#FFFFFF";
        ctx.font = "14px Microsoft YaHei";
        ctx.textAlign = "center";
        ctx.textBaseLine = "middle";
        ctx.rotate(index * arc + stepArc);
        if (this.data.length === 1) {
          key
            .split("")
            .reverse()
            .forEach((l, index) => {
              ctx.fillText(l, 0, -index * 20);
            });
        } else {
          key
            .split("")
            .reverse()
            .forEach((l, index) => {
              ctx.fillText(l, 0, -index * 20 - 50);
            });
        }

        ctx.restore();
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.wheelWrapper {
  position: relative;
  // overflow: hidden;
}

.btn {
  height: 80px;
  width: 80px;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100px;
  text-align: center;
  line-height: 80px;
  font-size: 20px;
  font-weight: bolder;
  cursor: pointer;
  &:hover {
    font-size: 30px;
  }
}

#wheel {
}
</style>




