<template>
  <div class="gohan">
    <!-- 可折叠 -->
    <div class="editors">
      <div class="tag" v-for="(name,index) in list" :key="index">{{ name}}
        <div class="removeBtn" @click="remove(index)">
          <i class="fa fa-close" />
        </div>
      </div>
      <input v-model="inputValue" placeholder="请输入备选方案" @keyup.enter="add" :maxlength="10" />
    </div>
    <div class="play">
      <myWheel :data="list"></myWheel>
    </div>
  </div>
</template>

<script>
import myWheel from "./myWheel";

export default {
  components: {
    myWheel
  },
  data() {
    return {
      inputValue: "",
      list: [],
      selected: ""
    };
  },
  methods: {
    add() {
      this.list.push(this.inputValue);
      this.inputValue = "";
      if (this.list.length > 20) {
        this.list.shift();
      }
    },
    remove(index) {
      this.list.splice(index, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
.gohan {
  flex: 1;
}
.play {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex: 1;
  flex-direction: column;
}
.editors {
  display: flex;
  flex-wrap: wrap;
  .tag {
    min-width: 100px;
    background: #7e7ece;
    color: white;
    margin: 5px;
    text-align: center;
    height: 30px;
    line-height: 30px;
    padding-left: 20px;
    padding-right: 20px;
    cursor: pointer;
    position: relative;
    &:hover {
      .removeBtn {
        display: block;
      }
    }
    .removeBtn {
      position: absolute;
      right: 5px;
      top: 0px;
      display: none;
    }
  }
}
</style>



