<template>
    <div class="nav">
        <ul>
            <li class="menu" v-for="(menu,index) in list" :key="index" @click="navigate(menu.url)">
                {{menu.name}}
            </li>
        </ul>
    </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          name: "Gohan!",
          url: "/Gohan"
        },
        {
          name: "Gallery",
          url: "/gallery"
        }
      ]
    };
  },
  methods: {
    navigate(url) {
      this.$router.push(url);
    }
  }
};
</script>


<style lang="scss" scoped>
.nav {
  width: 150px;
  flex-shrink: 0;
  height: 100%;
  .menu {
    height: 40px;
    cursor: pointer;
    line-height: 40px;
    color: #7e7ece;
    &:hover {
      background: #e1eaff;
    }
  }
}
</style>




