import Vue from 'vue';
import Router from 'vue-router';
import index from '@/components/index';
import gohan from '@/components/gohan';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: '',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/gohan',
      name: 'gohan',
      component: gohan
    }
  ]
});



